import React from 'react'
import Write from './Write'
import Read from './Read1'

const Selected = ({ selected }) => {

    console.log(selected)
       
    if (selected === 'write') {
        return (
             <Write/>
        )
    }

    if (selected === 'read') {
        return (
             <Read/>
        )
    }

    if (selected === 'insights') {
        return (
            <div>Insights</div>
        )
    }

    if (selected === 'viewall') {
        return (
            <div>ViewAll</div>
        )
    }

    return (
         <Write/>
    )
}

export default Selected
